},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-8 order-2 order-lg-1 mb-4 mb-lg-0"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("En el proceso de valoración de apoyos, es fundamental considerar los diversos tipos de asistencias personales y apoyos humanos que las personas con discapacidad pueden requerir. El grado de asistencia personal varía según las necesidades individuales. Algunas personas requieren apoyo humano para actividades específicas como la higiene personal o la administración de medicamentos, en momentos puntuales del día. Otras necesitan acompañamiento más cercano, que puede incluir apoyo en la alimentación, labores de aseo, desplazamientos o participación en otras actividades cotidianas. Este tipo de asistencia puede extenderse durante varias horas al día y brindarse directamente en el hogar. También existen personas que requieren acompañamiento permanente, que incluye asistencia en todas las actividades básicas de la vida diaria, como la higiene, la alimentación, la movilidad, el aprendizaje y la comunicación.")]),_c('div',{staticClass:"row align-items-center bloque-texto-c color-primario-claro p-4 mb-4"},[_c('div',{staticClass:"col-lg-2 d-none d-lg-block",attrs:{"data-aos":"zoom-in"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema4/img3.svg"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-left"}},[_vm._v("En estos casos, los apoyos pueden ser diversos. Por ejemplo, se puede contar con interpretación en lengua de señas colombiana en momentos específicos, o con el acompañamiento de un guía intérprete para personas sordociegas, que facilita tanto la comunicación como el desplazamiento. También pueden existir asistencias relacionadas con el ejercicio de derechos sexuales, que implican apoyo en decisiones íntimas y personales.")])])])]),_c('div'